

module MyWay.Services.Components {
    export interface ILibraryTechAgent {
        DonneesDelocalisees: DonneesDelocalisees.DonneesDelocaliseesTechAgent;
    }
}

module MyWay.Services.DonneesDelocalisees {

    /**
    * Classe encapsulant les outils de debug et de diagnostics des donnes dlocalises
    * @classe DonneesDelocaliseesTechAgent
    * fonction : deleteDbDonneesDelocalisees  private - Permet de supprimer la base de donnes IndexedDb des donnes dlocalises
    * fonction : getTDTById  private - Permet de rcuprer l'objet TDT correspondant  l'identifiant pass en paramtre dans le storeTDT
    * fonction : deleteTDTById  private - Permet de supprimer l'objet TDT correspondant  l'identifiant pass en paramtre dans le storeTDT
    */
    export class DonneesDelocaliseesTechAgent extends DonneesDelocaliseesAgent {

        /**
        * Constructeur de la classe DonneesDelocaliseesTechAgent
        */
        constructor() {
            super();
        }

        /**
         * Permet de supprimer et de rinitialiser la base de donnes IndexedDB des donnes dlocalises
         * @param callback {function} action  excuter une fois les donnes supprimes
         * @param callbackError {function} action  excuter en cas d'erreur (le message d'erreur est transmis en paramtre: messageErreur)
         */
        public reInitDbDonneesDelocalisees(callback?: () => void, callbackError?: (erreur: Erreur) => void): IPromesse<void> {
            return this.sendMessage(Messages.DonneesDelocaliseesMessageSubject.ReInitDbDonneesDelocalisees, <Messages.IDonneesDelocaliseesMessageContentReInitDbDonneesDelocalisees>{}).then(() => {
                if (callback) {
                    callback();
                }
            }, (pErreur: Erreur) => {
                if (callbackError) {
                    callbackError(pErreur);
                }
                throw pErreur
            });
        }

        /**
        * Permet de rcuprer l'objet TDT correspondant  l'identifiant pass en paramtre dans le storeTDT
        * @param idTDT {string} identifiant du TDT  rcuprer dans le storeTDT
        * @param callback {function} action  excuter une fois que l'objet TDT a t rcuprer (l'objet TDT rcupr est transmis en paramtre: objectTDT)
        * @param callbackError {function} action  excuter en cas d'erreur (le message d'erreur est transmis en paramtre: messageErreur)
        */
        public getTDTById(idTDT: string, callback?: (objectTDT: any) => void, callbackError?: (erreur: Erreur) => void): IPromesse<any> {
            return this.sendMessage(Messages.DonneesDelocaliseesMessageSubject.GetTDTById, <Messages.IDonneesDelocaliseesMessageContentGetTDTById>{ idTDT: idTDT }).then((objectTDT: any) => {
                if (callback) {
                    callback(objectTDT);
                }
            }, (pErreur: Erreur) => {
                if (callbackError) {
                    callbackError(pErreur);
                }
                throw pErreur
            });
        }

        /**
        * Permet de supprimer l'objet TDT correspondant  l'identifiant pass en paramtre dans le storeTDT
        * @param idTDT {string} identifiant du TDT  supprimer dans le storeTDT
        * @param callback {function} action  excuter une fois que l'objet TDT a t supprim
        * @param callbackError {function} action  excuter en cas d'erreur (le message d'erreur est transmis en paramtre: messageErreur)
        */
        public deleteTDTById(idTDT: string, callback: () => void, callbackError: (erreur: Erreur) => void) {
            return this.sendMessage(Messages.DonneesDelocaliseesMessageSubject.DeleteTDTById, <Messages.IDonneesDelocaliseesMessageContentDeleteTDTById>{}).then(() => {
                if (callback) {
                    callback();
                }
            }, (pErreur: Erreur) => {
                if (callbackError) {
                    callbackError(pErreur);
                }
                throw pErreur
            });
        }
    }

    Components.RegistryAgent.getInstance().register(DonneesDelocaliseesTechAgent);
}